/**
 *
 * ExportSystemButton
 *
 */

import React from 'react';
// import styled from 'styled-components';

import { FormattedMessage } from 'react-intl';
import messages from './messages';
import CbButton, { ICbButtonProps } from '../CbButton';
import { connect } from 'react-redux';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';
import { selectPackageStructureById } from 'containers/Console/selectors/ipm';
import AsyncFeedback from '../AsyncFeedback';

interface ExportSystemButtonProps extends ICbButtonProps {
  systemName: string;
}

class ExportSystemButton extends React.PureComponent<
  ExportSystemButtonProps & StateToProps,
  {}
> {
  render() {
    const { packageStructure: { hasFetched }, ...rest } = this.props;
    return (
      <CbButton
        {...rest}
        disabled={rest.disabled || !hasFetched}
        isLoading={rest.isLoading || !hasFetched}
      >
        {hasFetched ? (
          <FormattedMessage {...messages.export} />
        ) : (
          <AsyncFeedback
            messages={[
              <FormattedMessage {...messages.gatheringFiles} />,
              <FormattedMessage {...messages.stillWorking} />,
              <FormattedMessage {...messages.stillLoadingNote} />,
              <FormattedMessage {...messages.loading} />,
              <FormattedMessage {...messages.hangInThere} />,
            ]}
          />
        )}
      </CbButton>
    );
  }
}

const mapStateToProps = (state: AppState, ownProps: ExportSystemButtonProps) =>
  createStructuredSelector({
    packageStructure: selectPackageStructureById(ownProps.systemName),
  });

type StateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

export default connect<StateToProps>(mapStateToProps, null)(ExportSystemButton);



