/**
 *
 * AsyncFeedback
 *
 */

import React from 'react';
// import styled from 'styled-components';

interface AsyncFeedbackProps {
  messages: JSX.Element[];
  messageTime?: number;
}

interface IState {
  currentMessage: number;
}

class AsyncFeedback extends React.Component<AsyncFeedbackProps, IState> {
  static defaultProps: Partial<AsyncFeedbackProps> = {
    messageTime: 4000,
  };

  state: IState = {
    currentMessage: 0,
  };

  intervalHandler: NodeJS.Timer;

  switchMessage = () => {
    this.setState({
      currentMessage:
        this.state.currentMessage + 1 === this.props.messages.length
          ? 0
          : this.state.currentMessage + 1,
    });
  };

  componentDidMount() {
    this.intervalHandler = setInterval(
      this.switchMessage,
      this.props.messageTime,
    );
  }

  render() {
    return this.props.messages[this.state.currentMessage];
  }

  componentWillUnmount() {
    clearInterval(this.intervalHandler);
  }
}

export default AsyncFeedback;



